.section-title {
  background-color: #3039ea;
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

.button, .input {
  border: 2px solid #050951;
  border-radius: 5px;
}

.load-more {
  border: 2px solid #050951;
  border-radius: 5px;
  margin: 10px auto 0;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  gap: 5px;
  margin-bottom: 10px;
  display: flex;
}

.gallery-section {
  padding: 20px 100px;
}

.photo-card {
  flex-basis: calc(10% - 18px);
  transition: transform .5s;
}

.photo-card:hover {
  transform: scale(1.05);
}

.links {
  color: #000;
  width: 100%;
  border: 1px solid #000;
  border-radius: 20px;
  flex-basis: calc(20% - 4px);
  font-size: 12px;
  text-decoration: none;
  overflow: hidden;
  box-shadow: 5px 5px 2px 1px rgba(2, 2, 55, .227);
}

.small_img {
  width: 100%;
  min-height: 80px;
}

.info-item {
  text-align: center;
  margin: 0;
}

.info {
  padding: 5px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  display: none;
  position: absolute;
  overflow: hidden;
}

.loader {
  color: #625c5c;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: block;
  position: relative;
}

.loader:after {
  text-align: center;
  content: "Searching images...";
  z-index: 5;
  display: block;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 100%;
  background: linear-gradient(#3039ea 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

